<template>
<div id='content'>
  <router-link to="/assess_list" class="link-back"><img :src="iconreback"></router-link>
       <!--  -->
      <progressBar greenwidth='60%'></progressBar>
     
      <!---->
      <br />
      <div id='mdName' style='margin:10px auto;font-size: 18px;font-weight: bold;text-align: center;'>{{hwInfo.name}}</div>

      <div id='homework-mdAssess'>

        <div class='mdRequest'>
          <span></span><span id='mdReq'>{{hwInfo.requirement}}</span>
        </div>

        <div class='mdRound'>
          <span></span><span id='mdround'>{{hwInfo.round}}</span>
        </div>

        <div class='mdExtendFrom'>
          <span></span><span id='mdExtendFro'>{{extend_hwInfo}}</span>
        </div>

        <div class='mdDeadline'>
          <span></span><span id='mdDDL' style='color:red;'>{{hwInfo.modify_ddl}}</span>
        </div>


        <br />

        <div class='modHomework'>

          <!---->
          <div id='modHomework-mywork' class='panel panel-default'>
            <div class='panel-heading'>
              <span class='panel-title'></span>
            </div>
            <div class='panel-body'>
              <p>{{myHW.student_homework_content}}</p>
            </div>
          </div>

          <!---->
          <div id='modHomework-result' class='panel panel-default'>
            <div class='panel-heading'>
              <span class='panel-title'></span>

            </div>
            <div class='panel-body'>
              <div class='modHomework-score'>
                <span>(0-100)&nbsp;&nbsp;&nbsp;{{selfresult.avg_score}}</span>
                <span>&nbsp;&nbsp;&nbsp;</span>
              </div>

              <!---->
              <div id='assess-chart'></div>

              <div v-for='avgstar in selfresults.avg_star' class='modHomework-text'>
                <span id='modHW-text-label'>{{avgstar.standard}}&nbsp;&nbsp;
                &nbsp;&nbsp;{{avgstar.avg_star}}</span>
                <div class='table-responsive'>
                 <table id='modHomework-table' class='table  table-responsive'>
                  <tr class='info' v-for='(asssessment, index) in avgstar.assessments'>
                    <td class='modHW-table-left'>{{index}}</td>
                    <td class='modHW-table-right'>{{assessment}}</td>
                  </tr>
                 </table>
                </div>
              </div>

             

            </div>
          </div>

        </div>

        <div id='modHomework-mod' class='panel panel-default'>
          <div class='panel-heading'><span></span></div>
          <div class='panel-body'>
            <textarea class='form-control' rows='12' v-model='modify'></textarea>
          </div>
        </div>
      </div>

      <!---->
      <div id='modAssessBtn' class='center-block'>
        <button type='button' class='btn btn-success' @click='modAssessSubmit()'></button>
        <!---->
        <button type='button' class='btn btn-warning' @click='modAssessSave()'></button>
      </div>

    </div>

</template>

<script type='text/javascript'>
import axios from 'axios'
import store from '@/store.js'
// import AmCharts from 'amcharts'
import progressBar from '@/components/progressBar'
import stargrey from '@/assets/star-grey.png'
import staryellow from '@/assets/star-yellow.png'
import iconreback from '@/assets/reback.png'
export default {
  data () {
    return {
      homework_id: this.$route.params.id,
      hwInfo: [], // 
      extend_hwName: '', // 
      assessStanadrd: [], // 
      stargrey: stargrey,
      staryellow: staryellow,
      iconreback: iconreback,
      myHW: [], // 
      assessText: [], // 
      selfresults: {}, // 
      classresults: {}, // 
      succ: '', // 
      modify: ''
    }
  },
  components: {
    progressBar
  },
  methods: {
    getExtendName: function (hwid) { // ID
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_homework_info_by_id',
        method: 'post',
        data: {
          type: 'S3008',
          token: '1f5be77b086bc671b321a66ae4675330',
          homework_id: self.homework_id
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        self.extend_hwName = res.data.msg.name
        console.log(self.extend_hwName)
      }).catch(function (err) {
        console.log(err)
      })
    },
    getHWInf: function () { // 
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_homework_info_by_id',
        method: 'post',
        data: {
          type: 'S3008',
          token: '1f5be77b086bc671b321a66ae4675330',
          homework_id: self.homework_id
        },
        transformRequest: [function (data) {
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        self.hwInfo = res.data.msg
        this.getExtendName(self.hwInfo.extend_from)
        console.log(self.hwInfo)
      }).catch(function (err) {
        console.log(err)
      })
    },
    getAssesssDimen: function () {  // 
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_homework_standard',
        method: 'post',
        data: {
          type: 'S3004',
          token: 'fecf5b523f9e5f7c6fc6923f653edddc',
          homework_id: self.homework_id
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        self.assessStanadrd = res.data.msg
        console.log(self.assessStanadrd)
      }).catch(function (err) {
        console.log(err)
      })
    },
    getselfHw: function () { // 
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_four_homework',
        method: 'post',
        data: {
          type: 'S3002',
          token: '3406ae3f44399522653232e2c24ac811',
          homework_id: self.homework_id
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        self.myHW = res.data.msg
        console.log(self.selfHw)
      }).catch(function (err) {
        console.log(err)
      })
    },
    // 
    getselfResult: function () {
      // 
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_homework_personal_result',
        method: 'post',
        data: {
          type: 'S3013',
          token: 'fecf5b523f9e5f7c6fc6923f653edddc',
          homework_id: self.homework_id
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        self.selfresults = res.data.msg
        console.log(self.result)
      }).catch(function (err) {
        console.log(err)
      })
    },
    getclassResult: function () {
      // 
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_homework_class_result',
        method: 'post',
        data: {
          type: 'S3012',
          token: 'fecf5b523f9e5f7c6fc6923f653edddc',
          homework_id: self.homework_id
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        self.classresults = res.data.msg
        console.log(self.result)
      }).catch(function (err) {
        console.log(err)
      })
    },
    showChart: function (round) {
      var everydemin = []
      for (var i = 0; i < this.assessStanadrd.length; i++) {
        let demin = {
          deminsion: this.assessStanadrd[i].standard,
          class: this.classresults[this.hwInfo.round].avg_star[i].avg_star,
          personal: this.selfresults[this.hwInfo.round].avg_star[i].avg_star
        }
        everydemin.push(demin)
      }
     /* AmCharts.makeChart('assess-chart' + round, {
        'theme': 'light',
        'type': 'serial',
        'dataProvider': everydemin,
        'valueAxes': [{
          'unit': '',
          'position': 'left',
          'title': ' '
        }],
        'startDuration': 1,
        'graphs': [{
          'balloonText': ' [[category]] : <b>[[value]]</b>',
          'fillAlphas': 0.9,
          'lineAlpha': 0.2,
          'title': 'classResult',
          'type': 'column',
          'valueField': 'class'
        }, {
          'balloonText': ' [[category]] : <b>[[value]]</b>',
          'fillAlphas': 0.9,
          'lineAlpha': 0.2,
          'title': 'personResult',
          'type': 'column',
          'clustered': false,
          'columnWidth': 0.5,
          'valueField': 'personal'
        }],
        'plotAreaFillAlphas': 0.1,
        'categoryField': 'deminsion',
        'categoryAxis': {
          'gridPosition': 'start'
        },
        'export': {
          'enabled': true
        }
      }) */
    },
    // 
    getAssessText: function () {
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_assessment',
        method: 'post',
        data: {
          type: 'S3009',
          token: 'fecf5b523f9e5f7c6fc6923f653edddc',
          homework_id: self.homework_id
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        self.assessText = self.assessStanadrd.length
        for (let i = 0; i < res.data.msg.length; i++) {
          for (let j = 0; j < self.assessStanadrd.length; j++) {
            if (res.data.msg[i].standard_id === self.assessStanadrd[j].id) {
              self.assessText[j].push(res.data.msg[i])
            }
          }
        }
        console.log(self.assessText)
      }).catch(function (err) {
        console.log(err)
      })
    },
    modAssessSubmit: function () { // 
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/student/modify_homework',
        method: 'post',
        data: {
          type: 'S3006',
          token: 'fecf5b523f9e5f7c6fc6923f653edddc',
          student_homework_id: self.myHW.student_homework_id,
          modify: document.querySelector('#modHomework-mod textarea').value
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log(res.data.msg)
        console.log('')
      }).catch(function (err) {
        console.log(err)
      })
    },
    modAssessSave: function () { // 
      store.save('modify', this.modify)
      alert('')
    }
  },
  created () {
    this.getHWInf()
    this.getAssesssDimen()
    this.getmdHw()
    this.getclassResult()
    this.getselfResult()
    // this.showChart(this.hwInfo.round)
  }
}
</script>

<style type='text/css'>
.link-back{
  float:right;
  width:150px;
}
.link-back img{
  float: right;
}
/**/
#homework-modAssess span{
  font-weight: bold;
}
#homework-modAssess div{
  margin-top:10px;
}
/**/
#homework-modAssess .modHomework .panel-heading{
  margin-top:0px;
}
#homework-modAssess .modHomework .panel-body{
  margin-top:0px;
}
/**/
#modHomework-result .modHomework-score{
  display: inline;
}
#modHomework-result .modHomework-score span{
      margin-right:30%;
}

/**/
#modHomework-result .modHomework-text{
  margin-top:20px;
}

/**/
#modHomework-table {
  width:95%;
  margin-top:10px;
  margin-left: 2.5%;
}
#modHomework-table .modHW-table-left{
  width:5%;
}
#modHomework-table .modHW-table-right{
  width:95%;
}


/**/
#modHomework-mod .panel-heading,#modHomework-mod .panel-body{
  margin-top:0px;
}



/**/
#assess-chart {
  width   : 90%;
  height    : 300px;
  font-size : 11px;
}








/**/

#modAssessBtn button{
  width:100px;
  margin-bottom:20px;
  margin-left:25%;
}




</style>



